import React, { useState, useRef } from "react";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Typography,
  Button,
  Pagination,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import dayjs from "dayjs";
import BOPPListModal from "./BoPPListModal";
import ModalBox from "../ModalBox/ModalBox";

const BOPPList = ({ logsList, currentPage, setCurrentPage }) => {
  const ref = useRef();
  const [expandedItems, setExpandedItems] = useState({});
  const [docId, setDocId] = useState(null);

  const formatDate = (date) => date?.format("MM/DD/YYYY");

  const handleChange = (panel) => (_, isExpanded) => {
    setExpandedItems((prevExpandedItems) => ({
      ...prevExpandedItems,
      [panel]: isExpanded,
    }));
  };

  const handlePageChange = (_, newPage) => {
    setCurrentPage(newPage);
  };

  const paginatedData = logsList?.slice(
    (currentPage - 1) * 10,
    currentPage * 10
  );

  const handleOpenModal = (id) => {
    setDocId(id);
    ref?.current?.openModal();
  };

  return (
    <>
      {paginatedData?.map((item, index) => (
        <Accordion
          key={item?._id}
          expanded={expandedItems[`panel${item?._id}`]}
          onChange={handleChange(`panel${item?._id}`)}
          sx={{ marginBottom: 2 }}
        >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls={`panel${item?._id}-content`}
            id={`panel${item?._id}-header`}
          >
            <Box display="flex" justifyContent="space-between" width="100%">
              <Typography variant="h6">
                {`Check List ${index + 1}`} - <span>{formatDate(dayjs())}</span>
              </Typography>
            </Box>
          </AccordionSummary>
          <AccordionDetails>
            <Box sx={{ marginLeft: 2 }}>
              <Typography variant="body1">
                Shift Incharge: {item?.personnelObj?.shiftIncharge}
              </Typography>
              <Box mt={1}>
                <Typography variant="body1">
                  Operator: {item?.personnelObj?.operator}
                </Typography>
              </Box>
              <Box mt={1}>
                <Button
                  size="small"
                  variant="contained"
                  sx={{ cursor: "pointer", marginBottom: "4px" }}
                  onClick={() => handleOpenModal(item?._id)}
                >
                  View details
                </Button>
              </Box>
            </Box>
          </AccordionDetails>
        </Accordion>
      ))}
      <Box display="flex" justifyContent="center" mt={2}>
        <Pagination
          count={Math.ceil(logsList?.length / 10)}
          page={currentPage}
          onChange={handlePageChange}
          color="primary"
        />
      </Box>

      <ModalBox ref={ref} size="lg" title="">
        <BOPPListModal docId={docId} />
      </ModalBox>
    </>
  );
};

export default BOPPList;
