import React from "react";
import {
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  Paper,
} from "@mui/material";
import { useGetLogsDataQuery } from "../../Service/admin";
import { styled } from "@mui/system";

const formatKey = (key) => {
  let formattedKey = key.replace(
    /_([a-z])/g,
    (_, letter) => ` ${letter.toUpperCase()}`
  );
  formattedKey = formattedKey.replace(
    /__([a-z])/g,
    (_, letter) => `-${letter.toUpperCase()}`
  );
  return formattedKey.charAt(0).toUpperCase() + formattedKey.slice(1);
};

const formatData = (data) => {
  if (Array.isArray(data)) {
    return data.map(formatData);
  }

  if (typeof data === "object" && data !== null) {
    const formattedObj = {};
    for (const key in data) {
      if (data.hasOwnProperty(key)) {
        const formattedKey = formatKey(key);
        formattedObj[formattedKey] = formatData(data[key]);
      }
    }
    return formattedObj;
  }

  return data;
};

const StyledTableContainer = styled(TableContainer)(({ theme }) => ({
  marginTop: "20px",
  borderRadius: "8px",
  boxShadow: "inset 0px 4px 8px rgba(0, 0, 0, 0.1)"}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  borderBottom: `1px solid ${theme.palette.divider}`,
  padding: theme.spacing(1.5),
}));

const BoldTableCell = styled(StyledTableCell)({
  fontWeight: "bold",
  color: "#555",
});

const renderDataInTable = (data) => {
  if (typeof data === "object" && data !== null) {
    return Object.keys(data).map((key, index) => (
      <TableRow
        key={key}
        style={{ backgroundColor: index % 2 ? "#f9f9f9" : "#fff" }}
      >
        <BoldTableCell component="th" scope="row">
          {key}
        </BoldTableCell>
        <StyledTableCell>
          {data[key] !== null && data[key] !== undefined && data[key] !== ""
            ? typeof data[key] === "object"
              ? renderDataInTable(data[key])
              : data[key].toString()
            : "-"}
        </StyledTableCell>
      </TableRow>
    ));
  }
  return null;
};

const BOPPListModal = ({ docId }) => {
  const { data } = useGetLogsDataQuery({ docId });
  const formattedData = formatData(data?.docDetails);

  return (
    <div style={{ padding: "20px" }}>
      <Typography variant="h6" gutterBottom style={{ color: "#3f51b5" }}>
        BOPP Log Details :-
      </Typography>
      {formattedData ? (
        <StyledTableContainer component={Paper}>
          <Table>
            <TableBody>{renderDataInTable(formattedData)}</TableBody>
          </Table>
        </StyledTableContainer>
      ) : (
        <Typography variant="body2" color="textSecondary">
          No data available
        </Typography>
      )}
    </div>
  );
};

export default BOPPListModal;
